<template lang="html">
<div class="cartfooter">
  <el-popover
    ref="popover1"
    placement="bottom"
    trigger="click"
    width=300
    popper-class="popfixed"
    >
    <slot v-if="$store.state.fee==0"><span>还没有商品</span></slot>
    <slot v-if="$store.state.fee!=0">
      <div class="" style="display:flex;justify-content:flex-end;">
        <el-button type="text"  size="medium" @click.native="clear()"> 清空</el-button>
      </div>
        <div>

          <ul class="list-group" style="border-width:0px;">
            <li class="list-group-item" v-for="(goods,index) in $store.state.goods" v-if="goods.number " style="border-right-width:0;border-left-width:0;border-top-width:0px;border-bottom-width:2px;border-color:#eee;">
              <el-row>
                <el-col :xs="8">
                  <div class="grid-content bg-purple" style="display:flex;justify-content:flex-start;align-items:baseline">
                  <span style="margin-right:5px;">{{goods.goods_name}}</span><span style="color:rgb(255, 102, 0);font-size:x-small">x</span ><span style="font-size:x-small;color:#333;">{{goods.number}}</span>
                  </div>
                </el-col>
                <el-col :xs="8">
                  <div class="grid-content bg-purple" style="display:flex;justify-content:flex-end;">
                    <span>{{(goods.price * goods.number).toFixed(2)}}</span>
                  </div>
                </el-col>
                <el-col :xs="8" :pull="1" ><div class="grid-content bg-purple" style="display:flex;align-items:flex-end;justify-content:flex-end;">
                  <div class="addbutton" v-bind:class="{'wid':goods.number!=0}">
                    <span style="background:white;" class="el-icon-remove-outline" v-if="goods.number!=0" v-on:click="movefromcart(index)"></span>
                    <span class="number" v-if="goods.number!=0">{{goods.number}}</span>
                    <span class="el-icon-circle-plus " v-on:click="addtocart(index)"></span>
                  </div>
                </div></el-col>
              </el-row>
            </li>
          </ul>
        </div>
    </slot>
  </el-popover>
  <el-row>
  <el-col :span="6"><div class="grid-content bg-purple">
    <div class="circle" v-bind:class="{'changecart':$store.state.fee > 0}" v-popover:popover1 >
      <div class="">
        <img v-bind:src="img[0]" v-if="$store.state.fee <= 0" >
        <img v-bind:src="img[1]" v-if="$store.state.fee > 0" >
      </div>
    </div>
  </div></el-col>
  <el-col :span="12"><div class="grid-content bg-purple-light" style="display:flex;align-items:center;">
    <div class="Settlement">
      <span style="color:white; font-size:medium;">{{$store.state.fee}}</span>
    </div>
  </div></el-col>
  <el-col :span="6"><div class="grid-content bg-purple " v-bind:class="{'showBtn':$store.state.fee > 0}">
    <div class="">
      <span style="color:white;font-size:1.4em;"  v-if="$store.state.fee > 0" @click="open4" >去结算</span>
    </div>
  </div></el-col>
</el-row>

</div>
</template>

<script>
export default {
  name:"cartfooter",
  data(){
    return{
      img:['/static/img/zhaicart.png','/static/img/buycart.png'],
      show:false,
      payway:0
    }
  },
  methods:{
    addtocart(index){
      this.$store.dispatch("add",this.$store.state.goods[index].pivot.goods_id);
    },
    movefromcart(index){
      this.$store.dispatch("move",this.$store.state.goods[index].pivot.goods_id);
    },
    clear(){
      this.$store.dispatch("clear");
    },
    handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      open4() {
        this.$alert('<div style="text-align:center"><input v-model="payway" type="radio" name="payway" value="1">支付宝</input></div>'+
        '<div style="text-align:center"><input ref="payway" v-model="payway" type="radio" name="payway" value="2">微&nbsp&nbsp&nbsp信</input></div>'
        , '选择支付方式', {
          dangerouslyUseHTMLString: true,
          center:true,
          showCancelButton:true,
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          showClose:true,
          roundButton:true,
          cancelButtonClass:'cancel',
          customClass:'payBox',
          beforeClose:(action,instance,done) =>{
            if (action=="confirm") {
              let payway = document.getElementsByName('payway');
              for (var i = 0; i < payway.length; i++) {
                if (payway[i].checked) {
                  let para = {
                    payway:payway[i].value,
                    goods:this.$store.state.goods,
                    openid:this.$route.query.openid
                  };
                  this.$store.dispatch('pay',para);
                }
              }
            }
            done();
          }
          // callback: action => {
          //   this.$store.dispatch('pay');
          // }
        });
        }
    }
  }

</script>

<style lang="css">
*{outline:none;}
.cartfooter{
  background-color: rgba(61,61,63,.9);
  height: 3.7em;
}
.cartfooter >.el-row >.el-col >div{
  height: 3.7em;
}
.circle{
  height: 4em;
  width: 4em;
  border-radius: 50%;
  margin-top:-1em;
  display: inline-block;
  background-color: #363636;
  border-width: 5px;
  border-style: solid;
  border-color: #666;

}
.circle >div{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.circle >div >img{
  display: inline-block;
  width: 2em;
  height: 2em;
}
.Settlement >span::before{
  font-weight: 400;
  content:"\A5";
  font-size: medium;
  display:inline-block;
  margin-right: .2em;
}
.changecart{
  background-color: #3190e8;
}
.showBtn{
  display:flex;
  align-items:center;
  justify-content:center;
  background-color:#4cd964;
}
.wid{
  width: 50%;
}
.popfixed{
  position: absolute;
  bottom: 5em;
  height: auto;
  top: auto !important;
}
.cancel{
  background-color: #dcdfe6;
}
.payBox{
  width: 100% !important;
}
</style>



