<template lang="html">
  <div class="warn panel">
    <ul>
      <li v-for="(category) in $store.state.category" v-bind:id="'li_'+category.id">
        <div v-bind:id="'cate_'+category.id"><span>{{category.cate_name}}</span></div>
        <ul class="cate_items">
          <li  class="cate_item" v-for="(goods,index) in $store.state.goods" v-if="goods.category_id==category.id">
            <el-row>
              <el-col :xs="8"><div class="grid-content bg-purple">
                <div class="img-border">
                  <div><img v-bind:src="goods.img" class="img-responsive"></div>
                </div>
              </div></el-col>
              <el-col :xs="6" ><div class="grid-content bg-purple-light" style="height:5.6em;position:relative;">
                <div style="display:inline-block;height:auto"><span>{{goods.goods_name}}</span></div>
                <div style="display:inline-block;height:auto;position:absolute;bottom:0;left:0;">
                  <strong style="color:#f60;"><span class="price">{{goods.price}}</span></strong>
                </div>
              </div></el-col>
              <el-col :xs="9" :pull="1" ><div class="grid-content bg-purple" style="display:flex;flex-direction:row-reverse;height:5.6em;align-items:flex-end;">
                <div class="addbutton" v-bind:class="{'wid':goods.number!=0}">
                  <span style="background:white;" class="el-icon-remove-outline" v-if="goods.number!=0" v-on:click="movefromcart(index)"></span>
                  <span class="number" v-if="goods.number!=0">{{goods.number}}</span>
                  <span class="el-icon-circle-plus " v-on:click="addtocart(index)"></span>
                </div>
              </div></el-col>
            </el-row>
          </li>
        </ul>
      </li>

    </ul>
  </div>
</template>

<script>
export default {
  name:"category_content",
  data(){
    return{
    }
  },
  created(){
    if (!this.$route.query.openid) {
      window.location.href = "https://www.zhaischool.com/honest/#/?supplier_id=1";
    }
    else{
      this.$store.dispatch("loadsupplier",this.$route.query.supplier_id);
    }
  },
  methods:{
    addtocart(index){
      this.$store.dispatch("add",this.$store.state.goods[index].pivot.goods_id);
    },
    movefromcart(index){
      this.$store.dispatch("move",this.$store.state.goods[index].pivot.goods_id);
    }
  }
}
</script>

<style lang="css">
.warn{
  height: 45em;
  overflow-y: scroll;
}
.panel >ul, .panel >ul >li{
  list-style: none;
  padding: 0;
}
.panel >ul >li >div{
  width: 100%;
  border-bottom: 1px solid #f8f8f8;
  margin-bottom: 5px;
}
.panel >ul >li >div >span{
  font-size: 1em;
  line-height: 2em;
  text-indent: 1em;
  display: inline-block;
  -webkit-transform-origin-x: 0;
  -webkit-transform: scale(0.90);
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.cate_items{
  list-style: none;
  padding-left: 0;
}
.cate_item{
  height:auto;
  margin-left: .6em;
}
.img-border >div>img{
  width: 100%;
  display: inline-block;
}
.img-border{
  text-align: center;
  width: 5.6em;
  display: inline-block;
  height: 5.6em;
  background-color: #eee;
}
.img-border >div{

  margin-top: .3em;
  width: 5em;
  display: inline-block;
  height: 5em;
}
.cate_item >.el-row{
  margin-bottom: 1em;
}
.addbutton{
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  flex-wrap:nowrap;
  justify-content:space-between;
  height: auto;
}
.addbutton >span{
  float: right;
  color: #2395ff;
  font-size: large;
}
.number{
  font-size: small !important;
  color: #666 !important;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.wid{
  width: 50%;
}
.price::before{
  font-weight: 400;
  content:"\A5";
  font-size: .1em;
  display:inline-block;
  margin-right: .2em;
}
.price{
  font-size:medium;
}
</style>



