<template lang="html">

  <el-container>
    <el-header>
      <cartheader></cartheader>
    </el-header>
    <el-container class="contain">
      <el-aside width="30%"><category></category></el-aside>
      <el-main><categorycontent></categorycontent></el-main>
    </el-container>
    <el-footer><cartfooter></cartfooter></el-footer>
  </el-container>

</template>

<script>
import cartheader from "./common/header"
import cartfooter from "./common/footer"
import category  from "./content/category"
import categorycontent from "./content/category-content"
export default {
  name:"Cart",
  components:{
    cartheader,cartfooter,category,categorycontent
  },

}
</script>

<style lang="css">
body{
  margin:0;
  font-size: 100% !important;
}
.contain{

}
.el-main{
  padding: 0;
}
.el-aside{
background: #f8f8f8;
}
.el-header, .el-footer {
    height: auto !important;
    text-align: center;
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
  .el-footer{
    bottom: 0;
    position: fixed;
    background-color: #333;
    width: 100%;
  }
  .el-main {
    color: #333;
  }
</style>



