<template lang="html">
  <ul class="list-group items">
    <li v-for="(item,index) in this.$store.state.category" class="list-group-item item" name="item" v-on:click="choose(index)">
    <span>{{item.cate_name}}</span>
    </li>
  </ul>
</template>

<script>
export default {
  name:"category",
  data(){
    return {

    }
  },
  methods:{
    choose(index){
      let li = document.getElementsByName('item');
      for (var i = 0; i < li.length; i++) {
        if (i==index) {
          li[index].style.background="white";
          li[index].style.color="#333";
          document.querySelector("#cate_"+(index+1)).scrollIntoView();
        }
        else {
          li[i].style.background="#f8f8f8";
          li[i].style.color="#666";
        }
      }
    }
  }
}
</script>

<style lang="css">

.item{
  background: #f8f8f8;
  border-radius: 0;
  border-top-width: 0;
  border-bottom-width: 2px;
  border-right-width: 0;

}
.item >span{
  line-height: 2em;
  color: #666;
}
</style>



