<template lang="html">
  <div class="cartcontent" id="cartcontent">
    <el-popover
      ref="popover2"
      placement="bottom-end"
      trigger="click">
      <ul class="list-group" >
        <li class="list-group-item" v-for="(details) in $store.state.discount" style="border-width:0">
          <div class="discount">
            <div class="discount-title"> <span v-text="details.type=='supplier_favorable'?'店铺':'商品'"></span></div>
            <span class="discount-content">{{details.content}}</span>
          </div>
        </li>
      </ul>
    </el-popover>
    <el-row >
      <el-col >
        <div class="grid-content bg-purple-dark title">
          <span> {{$store.state.title}}</span>
        </div>
    </el-col>
    </el-row>

    <el-row :gutter="10" class="announcement">
      <el-col :push="1" :xs="4" :sm="4" :md="8" :lg="9" :xl="11"><div class="grid-content bg-purple-light">
        <span> <span>{{$store.state.tip}}</span> </span>
      </div></el-col>
      <el-col :xs="13" :sm="14" :md="8" :lg="9" :xl="11"><div class="grid-content bg-purple content">
      <span >{{$store.state.content}}</span>
      </div></el-col>
      <el-col :xs="6" :sm="6" :md="8" :lg="9" :xl="11"><div class="grid-content bg-purple info">
        <span class="detail_show">  <el-button v-popover:popover2 type="text">{{$store.state.info}}</el-button><i class="el-icon-caret-bottom"></i></span>
      </div></el-col>
    </el-row>
  </div>

</template>

<script>
export default {
  name:"cartcontent",
  data(){
    return {

    }
  }
}
</script>

<style lang="css">
.detail_show >.el-button{
  padding: 0;
}
  .cartcontent{
    margin-top: 40px;
    padding: 0 20px;
  }
  .title{
    text-align: center;
  }
  .announcement{
    border-style: solid;
    border-radius: 5px;
    border-width: 1px;
    border-color: #eee;
    margin-top: 1em;

  }
  .grid-content {
    min-height: 1.3em;
  }
  .bg-purple-dark >span{
    font-size: 1.5em;
  }
  .bg-purple-light >span{
    background:rgb(240, 115, 115);

    border-radius: 2px;

    display: flex;
    justify-content: center;
    align-items: center;

  }
  .bg-purple-light >span >span{
    color: white;
    font-size: 12px;
    -webkit-transform: scale(0.80);
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .el-button{
    padding: 0;
    border-width: 0;
  }
  .el-button >span{
    font-size: 12px;
    -webkit-transform-origin-x: 0;
    -webkit-transform: scale(0.90);
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .content{
    display: flex;
    justify-content: center;
    align-items: center;

  }
  .content >span{
line-height: 1.6em;
    white-space: nowrap;
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .info{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .info >span{


    white-space: nowrap;
    display: inline-block;

    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  .info >span >span{
    padding-right: 5px;
  }
  .discount{
    width: 15em;
    display: flex;
    justify-content: space-between;
  }
  .discount-title{
    display: flex;
    align-items: center;
  }
  .discount-title >span{
    font-size: .8em;
    -webkit-transform-origin-x: 0;
    -webkit-transform: scale(0.90);
    -webkit-font-smoothing:antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: rgb(240, 115, 115);
    color:white;
  }
  .discount-content{
    display: inline-block;
  }
</style>



