import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import VueAxios from 'vue-axios'


Vue.use(VueAxios, axios)
Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    goods:[],
    fee:0,
    category:[],
    title:"",
    tip:"诚信",
    content:"",
    info:"",
    supplier_img:"",
    supplier_id:"",
    discount:[
      {title:'商品',content:'饼干一限时打折'},
      {title:'满减',content:'满15减4'}
    ]
  },
  mutations: {
    addtocart(state, goods) {
      for (let i = 0; i < state.goods.length; i++) {
        if (state.goods[i].pivot.goods_id == goods) {
          state.goods[i].number++;
          state.fee=( parseFloat(state.fee) +parseFloat(state.goods[i].price)).toFixed(2);
          break;
        }
      }
    },
    movefromcart(state, goods) {
      for (let i = 0; i < state.goods.length; i++) {
        if (state.goods[i].pivot.goods_id == goods) {
          state.goods[i].number--;
          state.fee=( parseFloat(state.fee) - parseFloat(state.goods[i].price)).toFixed(2);
          break;
        }
      }
    },
    clearcart(state){
      for (let i = 0; i < state.goods.length; i++) {
        state.goods[i].number = 0;
      }
      state.fee = 0;
    },
    supplier(state,supplier_content){
      state.goods = supplier_content.data.data[0].goods;
      state.category =  supplier_content.data.data[0].category;
      state.title = supplier_content.data.data[0].name;
      state.content = '诚信率为'+supplier_content.data.data[0].honesty_rate;
      state.info = supplier_content.data.data[0].announcements.length +"个优惠";
      state.supplier_img = supplier_content.data.data[0].img;
      state.title = supplier_content.data.data[0].supplier_name;
      state.discount = supplier_content.data.data[0].announcements;
      state.supplier_id = supplier_content.data.data[0].id;
    },
    topay(){

    }
  },
  actions: {
    add(store, goods) {
      store.commit("addtocart", goods);
    },
    move(store, goods){
      store.commit("movefromcart", goods);
    },
    clear(store){
      store.commit("clearcart");
    },
    loadsupplier(store,supplier_id){
      Vue.axios.get("http://honesty.zhaischool.com/api/supplier/"+supplier_id).then(response=>{
        store.commit("supplier",response);
      })
    },
    pay(store,para){
      axios.post("http://honesty.zhaischool.com/api/supplier/"+this.state.supplier_id+"/buy",para).then(
        response=>{
          console.log(response);
          if (response.data.data.order_payway ==1) {
            window.location.href='https://www.zhaischool.com/mobile/pay/alipayapi.php?out_trade_no='+
            response.data.data.id+'&total_fee='+response.data.data.order_pay+'&honesty=1';
          }
          else if (response.data.data.order_payway==2) {
            window.location.href='https://www.zhaischool.com/mobile/weixinpay.php?honesty_out_trade_no=0'+
            response.data.data.id;
          }
        });
      // store.commit('topay',payway);
      // console.log(para);
    }
  }
})



