<template lang="html">
  <div class="tab">
    <el-row>
      <el-col :span="12" @click.native="change('buy','comment')"><div class="grid-content bg-purple">
        <span id="buy" >购买</span>
      </div></el-col>
      <el-col :span="12" @click.native="change('comment','buy')"><div class="grid-content bg-purple">
        <span id="comment">评价</span>
      </div></el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name:"content-tab",
  mounted:function(){
    this.change('buy','comment');
  },
  methods:{
    change(ele,unele){
      if (ele=='comment') {
        this.open();
      }
      document.getElementById(ele).style.color="black";
      document.styleSheets[0].addRule('#'+ele+'::after','background-color: #2395ff');
      document.styleSheets[0].addRule('#'+unele+'::after','background-color: #eee');
      document.getElementById(unele).style.color="#666";

    },
    open() {
        this.$message('暂时未开放，敬请期待');
      }
  }
}
</script>

<style lang="css">
.tab{
  margin-top: 20px;
}

.tab >.el-row{
  border-bottom: 2px solid #eee;
}
.tab >.el-row >.el-col >.grid-content >span{
  line-height: 2.5em;
  font-size: 1.2em;
  box-sizing: inherit;
  text-align: center;
  position: relative;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #666;
}
.tab >.el-row >.el-col >.grid-content >span::after{
  content: "";
  position: absolute;
  box-sizing: inherit;
  height: 2px;
  bottom: -.8em;
  width: 1.6em;
  left: 0;
}
</style>



