<template lang="html">
  <div class="">
    <div class="cartheader">
      <img v-bind:src="$store.state.supplier_img" >
    </div>
    <cartcontenttop></cartcontenttop>
    <cartcontenttab></cartcontenttab>
  </div>

</template>

<script>
import cartcontenttop from "./content-top"
import cartcontenttab from "./content-tab"
export default {
  name:"cartheader",
  data(){
    return{

    }
  },
  components:{
    cartcontenttop,cartcontenttab
  }
}
</script>

<style lang="css">
.cartheader{
  background-color: rgb(251,218,6);
  height: 60px;
}
.cartheader >img{
  height: 60px;
  width: auto;
  margin-top: 20px;
  z-index: 9999;
}

</style>



